*, *::before, *::after {
  box-sizing: border-box; }

a {
  text-align: center;
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px; }

@media (max-width: 991px) {
  .container {
    max-width: 720px; } }

@media (max-width: 768px) {
  .container {
    max-width: 372px; } }

.header {
  background: #AA0601;
  padding-top: 16px;
  padding-bottom: 16px;
  align-items: center; }

.logo a {
  background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/logo-white.png?raw=true);
  width: 80px;
  height: 50px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  text-indent: 101%;
  overflow: hidden;
  display: block; }

.menu {
  display: flex;
  justify-content: space-between; }

.menu a {
  color: white;
  display: block;
  font-size: 20px;
  padding: 10px 32px; }

.menu a:hover {
  color: #AA0601;
  background: white; }

.mr--32 {
  margin-right: -32px; }

.series {
  padding-bottom: 80px; }

.series-list {
  display: flex; }

.series-list li {
  width: 432px; }

.series-list a {
  font-size: 32px;
  color: #000000;
  padding: 24px 0px;
  display: block; }

.series-list a:hover {
  border-bottom: 8px solid #AA0601; }

.series-img {
  display: flex; }

.series-img-left {
  background-image: url(../images/product-header-1.png);
  width: 50%;
  height: 296px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat; }

.series-img-right {
  background-image: url(../images/product-header-2.png);
  width: 50%;
  height: 296px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat; }

.series-img li:hover {
  opacity: 0.9; }

@media (max-width: 991px) {
  .series {
    padding-bottom: 40px; }
  .series-list li {
    width: 256px; }
  .series-list a {
    font-size: 24px;
    padding: 16px 0px; }
  .series-img-left {
    background-image: url(../images/product-header-sm-1.png); }
  .series-img-right {
    background-image: url(../images/product-header-sm-2.png); } }

@media (max-width: 768px) {
  .series-list li {
    width: 125px; }
  .series-list a {
    font-size: 16px;
    padding: 12px 0px; } }

.combi {
  padding-bottom: 80px; }

.combi h2 {
  font-size: 42px;
  text-align: center; }

.sub-title {
  font-size: 32px;
  text-align: center;
  margin-bottom: 32px; }

.product {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

/* .product img{
    width: 306px;
    height: 168px;
} */
.product li {
  width: 24%;
  margin-bottom: 80px; }

.product a:hover {
  opacity: 0.9; }

.info {
  display: flex;
  justify-content: space-between; }

.square {
  display: flex;
  align-items: center; }

.square::before {
  content: '';
  width: 24px;
  height: 24px;
  background-color: #5F3E2D;
  border-radius: 4px;
  margin-right: 8px; }

.square::after {
  content: '';
  width: 24px;
  height: 24px;
  background-color: #B75929;
  border-radius: 4px; }

@media (max-width: 991px) {
  .product li {
    width: 49%;
    margin-bottom: 40px; } }

@media (max-width: 768px) {
  .combi {
    padding-bottom: 64px; }
  .combi h2 {
    font-size: 24px; }
  .sub-title {
    font-size: 24px;
    margin-bottom: 24px; }
  .product li {
    width: 100%;
    margin-bottom: 24px; } }

.page-box {
  display: flex;
  justify-content: center; }

.page {
  display: flex;
  border: 1px solid black; }

.page a {
  color: black;
  font-size: 18px;
  padding: 12px 16px;
  display: block; }

.page a:hover {
  color: #FFFFFF;
  background: #000000; }

.boder-right {
  border-right: 1px solid black; }

@media (max-width: 768px) {
  .page a {
    font-size: 16px;
    padding: 12px 14px; } }

